<template>
  <div>
    <h3>当前求和为：{{ $store.state.sum }}</h3>
    <select v-model="n">
      <option :value="1">1</option>
      <option :value="2">2</option>
      <option :value="3">3</option>
      <option :value="4">4</option>
    </select>
    <button @click="increment">+</button>
    <button @click="decrement">-</button>
    <button @click="incrementOdd">当为奇数时相加</button>
    <button @click="incrementWait">等待相加</button>

    <h1 id='1' class="ia">
        <a id = 'aii' class="ai">'随便试试'</a>
      </h1>
    <!-- <router-link :to="`/value?id=${message[0].id}`">传值连接</router-link> -->
  </div>
</template>

<script>
export default {
  name: "Calculator",
  data() {
    return {
      //   sum: 0,
      n: 1,
      message: [
        {id: 1}
      ]
    };
  },
  mounted() {
    console.log(document.getElementById('1').innerHTML);
  },
  methods: {
    increment() {
      this.$store.commit("JIA", this.n);
    },
    decrement() {
      this.$store.commit("JIAN", this.n);
    },
    incrementOdd() {
      this.$store.dispatch("jiaodd", this.n);
    },
    incrementWait() {
      setTimeout(() => {
        this.$store.commit("JIA", this.n);
      }, 500);
    },
  },
};
</script>

<style scoped>
.ai{
  /* display: inline-block; */
  font-size: 30px;
}
.ia > a{
  /* display: inline-block; */
  font-size: 60px;
}
</style>


